@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap);
*{
  padding: 0;
  margin: 0;
  font-family: "Jost",sans-serif;
}

body{
  background: #f3f2ef;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100vh;
  width: 100vw;
  font-family: "Jost",sans-serif;
}
html,body{
  font-size: 62.5%;
  height: 100%;
}

html{
  background: #eee;
}

.box{
  width: 20vw;
  height: 60vh;
  border-radius: 0.5rem;
  box-shadow: 0 0.2rem 3rem rgba(0, 0,0,0.2);
  background: #a5bbdd;
  position: relative;
  overflow: hidden;
  transform: translate3d(0,0,0);
  min-width: 20rem;
  min-height: 35rem;
}

.wave{
  opacity: 0.3;
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -25rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3500ms infinite linear;
  z-index: 1;
  

}
.wave-three{
    position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -25rem;
  transform-origin: 50% 48%;
  border-radius: 43%;

  
  animation: drift 4000ms infinite linear;

  opacity: 0.2;
  z-index: 2 !important
  
}
.wave-two{
    position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -25rem;
  transform-origin: 50% 48%;
  border-radius: 43%;

  
  animation: drift 2000ms infinite linear;
  opacity: 0.1;
  z-index: 3 !important
  
}
@keyframes drift{
    from{
      transform: rotate(0deg);
  
    }
    to{
      transform: rotate(360deg);
    }
  
  }
/* .wave-one{
  animation: drift 700ms infinite linear;
  opacity: 0.2;
  z-index: 2 !important
  
} */

.box:after{
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 11;
  transform: translate3d(0,0,0);

}

.info{
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 45%;
  z-index: 4;
}
.location{
  margin-top: -2rem;
  text-align: center;

  font-size:4.5rem;
  font-weight: 800;

}
.fa-street-view{
  animation: rotates 3s linear infinite alternate;

}
@keyframes rotates{
  from{
    transform: translateX(-0.5rem);
  }
  to{
    transform: translateX(0.5rem);
  }
}
#date{
  text-align: center;
  margin-top: 0.5rem;
  color: #57606f;
  font-size: 1.2rem;
  font-weight: 300;
  text-transform: uppercase;
}


.temp{
  margin-top: 1.3rem;
  text-align: center;
  font-size: 3.5rem;

}
.tempmin_max{
  text-align: center;
  margin-top: 0.3rem;
  font-weight: 300;
  font-size: 1.2rem;
  color: #57606f;
}
#weathercon{
  height: 55%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3em;
}
#weathercon .fas{
  font-size: 6rem;
  animation: fas-anime 3s linear infinite alternate;
}
@keyframes fas-anime{
  from{
    transform: scale(1.1);
  }
  to{
    transform: scale(1.5);
  }

}
@media(max-height:600px){
  .box{height: 80vh;
    width: 90vw;

  }
  .wave{
    top: 85%;
  }
  #weathercon{
    font-size: 5em;
  }
  .info{
    font-size: 1.5rem;
  }

}
@media(max-height:500px){
  .box{height: 80vh;

  }
  .wave{
    top: 115%;
  }
}
  body>span{
    width: 100vw;
    text-align: center;
    color: gray;
  }

.inputData{
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputFeild{
  width: 50%;
  margin: auto;
  border-radius: 5rem;
  padding: 1rem 1rem;
  outline: none;
  font-size: 1.5rem;

}
.errorMsg{
  text-align: center;
  font-size: 2rem;
  margin: 2rem auto;
}
